import { useMemo } from "react";
import { utils, writeFile } from "xlsx";
import { ExpBtn } from "../page/components/InputElement";

export default function ExpExcel (props) {
    const { title, countData, gachaCount, getChara, countRatio } = props;
    const data = useMemo(() => {
        let data = [];
        data[0] = [title.today, title.target, title.no];
        data[1] = ["", Object.values(countData).reduce((p,n)=>p+n, 0)];
        data[2] = ["", gachaCount.once, "", gachaCount.golden];
        data[3] = ["", gachaCount.ten, "LM", gachaCount.limit];
        data[4] = ["", gachaCount.gachapin, "LM", gachaCount.golden];
        data[5] = ["", gachaCount.mukr, "", gachaCount.season];
        data[6] = ["", gachaCount.other, "", gachaCount.season_golden];
        data[7] = [];
        data[8] = ["", "", "", ""];
        data[9] = ["", countData.ssr_w, countData.ssr_s, `${countRatio((countData.ssr_s+countData.ssr_w),(Object.values(countData).reduce((p,n)=>p+n,0)))}%` ];
        data[10] = ["", countData.sr_w, countData.sr_s, `${countRatio((countData.sr_s+countData.sr_w),(Object.values(countData).reduce((p,n)=>p+n,0)))}%`];
        data[11] = ["", countData.r_w, countData.r_s, `${countRatio((countData.r_s+countData.r_w),(Object.values(countData).reduce((p,n)=>p+n,0)))}%`];
        data[12] = ["", countData.ssr_w+countData.sr_w+countData.r_w, countData.ssr_s+countData.sr_s+countData.r_s, Object.values(countData).reduce((p,n)=>p+n,0)];
        data[13] = [];
        data[14] = ["", "", ""];
        data[15] = [getChara.normal, "", title.today];
        data[16] = ["Limited", "", ""];
        data[17] = [getChara.limited, "", title.target];
        data[18] = ["", "", ""];
        data[19] = [getChara.stone, "", title.no];
        data[20] = ["https://github.com/neko0104/gbfGacha"];
        return data;
    }, [title, countData, gachaCount, getChara]);

    const exportFile = () => {
        const ws = utils.aoa_to_sheet(data);
        const wb = utils.book_new();
        utils.book_append_sheet(wb, ws, "")
        writeFile(wb, "gbfGacha.xlsx")
    }

    return <ExpBtn text="excel" mission={exportFile} />;
}